<template>
	<el-row>
		<el-col :span="16">
			<article>
				<div class="title">
					<h1>
						{{article.title}}
					</h1>
				</div>
				<div class="label">
					<span v-for="tag in article.tagSet">
						{{tag.name}}
					</span>
				</div>
				<div class="content" v-html="toHtml()">
				</div>
			</article>
		</el-col>
		<el-col :span="8">
			<my-aside class="a-aside"></my-aside>

		</el-col>
	</el-row>

</template>

<script>
	import { Row, Col, Breadcrumb, BreadcrumbItem } from 'element-ui'
	import Aside from '../public/Aside.vue'
	import { markdown } from 'markdown';
	export default {
		name: 'article',
		components: {
			'el-row': Row,
			'el-col': Col,
			'el-breadcrumb': Breadcrumb,
			'el-breadcrumb-item': BreadcrumbItem,
			'my-aside': Aside,
		},
		created() {
			var urlStr = location.href;
			id = urlStr.substring(urlStr.lastIndexOf("/") + 1, idurlStrlength);
			console.log(id);
			this.getArticle(id);
		},
		methods: {
			toHtml() {
				return markdown.toHTML(this.article.mdContent);
			},
			getArticle(id) {
				this.$http.get('/article/get' + id).then((response) => {
					this.article = response.data;
					this.$http.get('/article/getTags/' + id).then((response) => {
						response.data.forEach(function(tag) {
							this.article.tagSet.push(tag.name);
						})

					}, (response) => {
						console.log('获取文章标签失败');
					});
				}, (response) => {
					console.log('获取文章失败');
				});
			}
		},
		data() {
			return {
				article: {},
			}
		}
	}
</script>

<style>
	.el-row {
		max-width: 1200px;
		min-width: 800px;
		margin: 0 auto;
		padding: 0px 100px 40px;
		transition: padding ease .5s;
	}
	
	.el-col:first-child {
		background-color: #FFFFFF;
		/*border: 1px solid #EEEEEE;*/
		padding: 10px;
	}
	
	@media only screen and (min-width: 0px) and (max-width: 960px) {
		.el-row {
			padding: 0px 0 40px;
		}
	}
	
	.title h1 {
		font-size: 28px;
		padding: 20px 0 0px;
	}
	
	article .label span {
		display: inline-block;
		font-size: 13px;
		border: 1px solid #20A0FF;
		padding: 4px 8px;
		border-radius: 40px;
		cursor: pointer;
		color: #1D8CE0;
	}
	
	.content {
		padding-right: 50px;
		min-height: 600px;
	}
	
	.a-aside {
		padding-top: 100px;
	}
	/*pre强制换行*/
	/*pre {
		white-space: pre-wrap!important;*/
	/*保留空白，进行换行*/
	/*	word-wrap: break-word!important;*/
	/*连续字符换行*/
	/*white-space:normal!important;*/
	/*忽略空白，进行换行*/
	/*	}*/
</style>


